body {
  font-family: 'Inter', sans-serif;
  color: #18161b;
  font-size: 14px;
  line-height: 1.125;
  font-weight: 400;
  margin: 0;
}

body.locked {
  overflow: hidden;
}

a {
  color: #18161b;
  transition: all 400ms ease;
}

a,
a:hover,
a:focus,
a:visited {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Bricolage Grotesque', sans-serif;
}

::placeholder {
  color: inherit;
  opacity: 1;
}

body.subpage {
  background-color: #f7f7f8;
}

.container-fluid,
.container {
  padding-left: 15px;
  padding-right: 15px;
}
